.header {
  position: fixed;
  display: flex;
  flex-direction: column;
  justify-content: center;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 50px;
  background-color: white;
  box-shadow: 0px 0px 20px 2px rgba(0, 0, 0, 0.1);
}

.header img {
  width: 150px;
  height: 25px;
  margin-left: 20px;
}
